html, body {
  margin: 0;
  padding: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.horizontal-scroll-slider {
  background: #f2f2f3;
  height: 100vh;
}

.horizontal-scroll-slider h2 {
  text-align: center;
  padding: 40px 10px;
  font-size: 80px;
}

.horizontal-scroll-slider .slides {
  flex-direction: row;
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
}

.horizontal-scroll-slider .slides .slide {
  background-color: #fff;
  border: 2px solid #000;
  width: 300px;
  height: 400px;
  margin-right: 20px;
  padding: 20px;
}

.horizontal-scroll-slider .text {
  background-color: #fff;
  border: 2px solid #000;
  justify-content: center;
  align-items: center;
  width: 5000px;
  padding: 40px;
  font-size: 40px;
  display: flex;
}

